	module.exports = {"data":{"markdownRemark":{"html":"<h2>Graphs</h2>\n<p>A graph is a data-structure you can use to solve routing problems, such as “Are these two components connected?” and “What is the shortest path from point a to b?”</p>\n<p>A graph consists of nodes and edges.\nA node (i.e. vertex) is an object in your graph.\nA node can contain information such as the name of the node and which edges it is attached to.\nAn edge is a link that connects two nodes.\nThe edge can contain information such as the weight of the edge.\nIf two nodes are connected by an edge, they are neighbours (i.e. adjacent).</p>\n<p>Depending on the problem you can use either two-way (undirected) or one-way (directed) edges.\nIf you have a undirected edge from a to b, there is also a way from b to a.\nIf you have a directed edge from a to b, there is not necessarily an edge from b to a.</p>\n<p>You can use graphs to formulate situations such as:</p>\n<ul>\n<li>\n<p>Geographical maps </p>\n<ul>\n<li>Each city in your country is a node</li>\n<li>\n<p>If two cities are connected by a road there is a edge between them</p>\n<ul>\n<li>Roads can be one- or two-way (both directed and undirected edges)</li>\n<li>the weight can be the lenght of the road</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>\n<p>Flow of water</p>\n<ul>\n<li>Each floodgate is a node</li>\n<li>\n<p>Each canal is an edge</p>\n<ul>\n<li>water will only flow in one direction so the edges are directed</li>\n<li>the weight can be the maximum water capacity of the flow</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<p>Example: a graph that has as nodes the capitals of the Nordic countries, and as (undirected) edges the driving distance to cities connected by direct road.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>.     +---------+\n.     |Reykjavik|\n.     +---------+\n.\n.\n.         529 km   +---------+  1760 km  +--------+\n.    +------------+|Stockholm|+---------+|Helsinki|\n.    |             +---------+           +--------+\n.    +                                        +\n. +----+                    1991 km           |\n. |Oslo|+-------------------------------------+\n. +----+\n.         +----------+\n.         |Copenhagen|\n.         +----------+</code></pre>\n      </div>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href='https://github.com/freecodecamp/guides/tree/master/src/pages/algorithms/graph-algorithms/breadth-first-search/index.md' target='_blank' rel='nofollow'>Breadth First Search (BFS)</a></p>\n<p><a href='https://github.com/freecodecamp/guides/tree/master/src/pages/algorithms/graph-algorithms/depth-first-search/index.md' target='_blank' rel='nofollow'>Depth First Search (DFS)</a></p>","fields":{"slug":"/computer-science/data-structures/graphs/"},"frontmatter":{"title":"Graphs"}}},"pathContext":{"slug":"/computer-science/data-structures/graphs/"}}

